"use scrict";

export default {
	model: {
		prop: "value",
		event: "change"
	},
	props: {
		value: {
			type: String,
			default: ""
		},
		placeholder: {
			type: String,
			default: "搜索"
		}
	},
	computed: {
		bindValue: {
			get() {
				return this.value;
			},
			set(value) {
				this.$emit("change", value);
			}
		}
	}
}
